$(document).ready( function() {
    
    // slickBox IMG *************************************
    var slickImg = $('.pages .slickBox').find('.slickImg')
    
    function slick_rwd(){
        var slicH = $('.pages .slickBox').height()
        slickImg.each(function(){
            var winH = $(window).height()
            var winW = $(window).width()
            // var slickIimg_w = $(this).data('width')
            // var slickIimg_h = $(this).data('height')
            var slickImgI = $(this).find('img')
            var slickImgDir = $(this).data('dir')

            // slickImg.css({height: slicH+'px!important'})
            
            // slickImgI.css({'max-width': slickIimg_w, 'max-height': slickIimg_h})

            if(winW >= winH){
                if(slickImgDir == 'vertical'){
                    slickImgI.css({ height: '100%', width: 'auto'})
                }else{
                    slickImgI.css({ height: 'auto', width: '100%'})
                }
            }else{
                if(slickImgDir == 'horizon'){
                    slickImgI.css({ height: 'auto', width: '100%'})
                }else{
                    slickImgI.css({ height: 'auto', width: '100%'})
                }  
            }
        })
    }
    
    $(window).resize(function(){
        slick_rwd()
    }).resize()
    

});


