import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import indexPage from '@/components/index'
import pages from '@/components/pages'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'indexPage',
      component: indexPage
    },
    {
      path: '/editorial',
      name: 'editorial',
      component: pages
    },
    {
      path: '/advertising',
      name: 'advertising',
      component: pages
    },
    {
      path: '/mens_grooming',
      name: 'mens_grooming',
      component: pages
    },
    {
      path: '/wedding',
      name: 'wedding',
      component: pages
    }
  ]
})



